

* {
  box-sizing: border-box;
}



.main-home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-size: 3rem;
}

.wrap {
  position: relative;
  width: 100%;
  height: 100%;
  transition: 0.5s;
  display: flex; /* 요소들을 가로로 배열하기 위해 추가 */
  flex-direction: column;
}

.container {
  flex: 1; /* 요소들이 동일한 너비로 설정되도록 함 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.container:nth-child(1) {
  background-color: rgb(255, 255, 255);
}

.container:nth-child(2) {
  background-color: rgb(255, 255, 255);
}

.container:nth-child(3) {
  background-color: rgb(255, 255, 255);
}

.container:nth-child(4) {
  background-color: lightpink;
}

.fixed-area {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
/* 공통 스타일 */
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
/* 애니메이션 효과들  */
/* (1) 커졌다 작아졌다 */
@keyframes animate {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
/* (2) 페이드인 */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
/* (3) 페이드아웃 */
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
